export default class MultipleSources {
    constructor() {
        this.allData = {};
        this.requiredFields = [
            'title',
            'city',
            'publisher',
            'year'
        ];
    }

    /**
     * Sets title.
     *
     * @param {string} data
     * @returns {void}
     */
    setTitle = (data = null) => {
        this.allData.title = data;
    };

    /**
     * Sets city.
     *
     * @param {string} data
     * @returns {void}
     */
    setCity = (data = null) => {
        this.allData.city = data;
    };

    /**
     * Sets publisher.
     *
     * @param {string} data
     * @returns {void}
     */
    setPublisher = (data = null) => {
        this.allData.publisher = data;
    };

    /**
     * Sets year.
     *
     * @param {string} data
     * @returns {void}
     */
    setYear = (data = null) => {
        this.allData.year = data;
    };
}
